import categoriesJson from '../assets/categories.json'
import itemsJson from '../assets/items.json'

const items = itemsJson.items.map(
    it => {
        if (it.sizes.length > 0) {
            it.sauces = [
                {id: "none", text: 'без соуса', selected: true },
                {id: "tomato", text: 'томатный', selected: false },
                {id: "balsamiq", text: 'бальзамик', selected: false },
                {id: "garlick", text: 'чесночный', selected: false },
            ];
        } else {
            it.sauces = [];
        }
        it.id = `${it.category}-${it.image}`;
        return it;
    }
);        
const filtered = items;

export default {
    isMobile: /Android|webOS|iPhone|iPod|Opera Mini/i.test(navigator.userAgent),        
    showDelivery: false,
    validToOrder: true,
    orderSent: false,
    fio: '',
    validFio: true,
    comment: '',    
    deliveryType: 0, //0 - доставка сейчас, 1 - доставка ко времени, 2 - самовывоз
    time: '',
    validTime: true,
    phone: '',    
    validPhone: true,
    street: '', 
    validStreet: true,    
    building: '',
    validBuilding: true,    
    entrance: '',
    floor: '',
    room: '',    
    callRequire: false,
    latitude: 0.0,
    longitude: 0.0,
    polygons: [
        {
            id: "1",
            mail: "mapizza@bk.ru",
            text: "Нагатинский",
            worktime: "11:00 - 23:00",
            path: [
                { lat: 55.690264, lng: 37.604464 },
                { lat: 55.689113, lng: 37.614007 },
                { lat: 55.691472, lng: 37.627276 },
                { lat: 55.683762, lng: 37.634523 },
                { lat: 55.686179, lng: 37.671218 },
                { lat: 55.692709, lng: 37.694694 },
                { lat: 55.684826, lng: 37.711842 },
                { lat: 55.677748, lng: 37.706024 },
                { lat: 55.668395, lng: 37.674382 },
                { lat: 55.654922, lng: 37.669993 },
                { lat: 55.658723, lng: 37.593034 },
                { lat: 55.670410, lng: 37.585787 },
            ]
        },
        {
            id: "2",
            text: "Анохина",
            mail: "mapizza.anohina@gmail.com",
            worktime: "10:00 - 22:00",
            path: [
                { lat: 55.663738, lng: 37.483174 },
                { lat: 55.665480, lng: 37.464361 },
                { lat: 55.667327, lng: 37.458371 },
                { lat: 55.671022, lng: 37.451444 },
                { lat: 55.673820, lng: 37.458651 },
                { lat: 55.685958, lng: 37.477465 },
                { lat: 55.675245, lng: 37.485140 },
                { lat: 55.670600, lng: 37.494593 },
                { lat: 55.667222, lng: 37.487293 },
            ]
        },
    ],
    polygonText: "Нагатинская",
    polygonMail: "mapizza@bk.ru",
    polygonWorktime: "11:00 - 23:00",
    inPolygons: 0, //0 - не задан, 1 - входит в зоу, 2 не входит в зону
    map: {},
    addrSearch: '',    
    items: items,
    filtered: filtered,
    categories: categoriesJson.categories,
    ordered: [],        

    getOrderKey(item) {
        const id = item.id;
        const size = item.sizes.find(it => it.selected == true);
        const sizeId = size ? size.id : 0;
        const thickness = item.thickness.find(it => it.selected == true);
        const thicknessId = thickness ? thickness.id : 0;
        const sauce = item.sauces.find( it => it.selected == true);
        const sauceId = sauce ? sauce.id : 0;
        return `${id}:${sizeId}:${thicknessId}:${sauceId}`;
    },    
    getItem(id) {
        return this.items.find(it => it.id === id)
    },
    getFilteredItem(id) {
        return this.filtered.find(it => it.id === id)
    },
}


