<template>
    <div id="order" v-show='getOrderItems.length > 0'>        
        <br>
        <h2>КОРЗИНА</h2>   
        <table id="order-items">            
            <app-order-item :key="item.orderKey" v-for="item in getOrderItems" :item='item' />                
            <tr class="total">                
                <td class="name">ИТОГО:</td>
                <td></td>
                <td class="price">{{getOrderSum}} ₽</td>
            </tr>
        </table>
        <app-contact-form id="order-form"/>
        <br>
        <app-delivery-form />        
        <button v-if='!isOrderSent' id="send-order" @click='onSend'>ЗАКАЗАТЬ ПИЦЦУ</button>
        <h3 v-else class="thank">Спасибо. Ваш заказ отправлен. Мы свяжемся с Вами в ближайшее время</h3>
        <br><br>        
    </div>    
</template>
<script>    
    import OrderItem from './OrderItem';
    import ContactForm from './ContactForm';
    import { mapGetters, mapMutations, mapActions } from 'vuex';
    import DeliveryForm from './DeliveryForm.vue';
    export default {
        components: {
            'app-order-item': OrderItem,
            'app-contact-form': ContactForm,
            'app-delivery-form': DeliveryForm,
        },
        methods: {            
            onSend(event) {                
                this.$store.commit('formValidation');                
                if (this.$store.state.validToOrder) {
                    console.log("valid form");                
                    this.$store.dispatch('sendMail');
                }
            }
        },
        computed: {
            ...mapGetters([
                'getOrderItems',
                'getOrderSum',                
                'isOrderSent',
            ]),           
        }         
    }
</script>
<style scoped>

    h2 {
        text-align: left;        
    }
    #order {        
        width: 100%;
        max-width: 840px;        
        padding: 10px;                
    }        
    #order-items {
        margin-top: 20px;
        margin-bottom: 40px;        
        width: 100%;
    }
    #send-order {
        border: none;
        display: block;
        border-radius: 10px;
        background-color: #f0712c;
        color: white;
        width: 300px;        
        padding: 15px;
        margin-right: 10px;
        margin-top: 20px;        
        transition: all 0.1s ease;
    }
    #send-order:hover {
        background-color: #de601b;
    }
    .thank {
        color: green;        
        padding: 10px;
    }
    
    .total td.price {
        font-size: 1.3em;
        text-align: right;            
        padding-right: 5px;
    }
    .total td.name {
        font-size: 1.3em;
        text-align: left; 
        padding-top: 20px;   
        padding-bottom: 20px;
    }
</style>


