<template>
    <tr class="order-item">
        <!-- <td><img class="image" :src="images('./' + item.image + '100.png')" /></td> -->
        <td class="text-container">
            <div class="name">{{item.name | uppercase}}</div>                        
            <!-- <div class="description" v-if="item.description.length > 0">{{item.description | lowercase}}</div>                              -->
            <div class="description" v-if="item.sizes.length > 0">
                <span v-show='size.selected' :key='size.id' v-for='size in item.sizes'>{{size.text | lowercase}}</span>,
                <span v-show='th.selected' :key='th.id' v-for='th in item.thickness'>{{th.text | lowercase}} тесто</span>                                
            </div>
            <div class="description" v-if="item.sizes.length > 0">
                <span v-show='item.sauces.length > 0'>соус: </span>
                <span v-show='sc.selected' :key='sc.id' v-for='sc in item.sauces'>{{sc.text | lowercase}}</span>
            </div>
            
        </td>
        <td class="qauntity-cont" v-if='item.price > 0'>
            <button v-if='item.orderKey !== "bonus"' class="small-button" @click="delFromOrderByKey(item.orderKey)">&#8722;</button>
            <span class="quantity">{{item.quantity}}</span>
            <button v-if='item.orderKey !== "bonus"' class="small-button" @click="addToOrder(item)">&#65291;</button>
        </td>        
        <td class="price" v-if='item.price > 0'>{{item.price * item.quantity}} ₽</td>        
        <td class="price" colspan="2" v-if='item.price == 0'>БЕСПЛАТНО</td>
        <td>
            <button class="small-button" @click="delAllFromOrderByKey(item.orderKey)">&#10005;</button>
        </td>
    </tr>    
</template>

<script>  
    import ProductParam from './ProductParam';  
    import { mapMutations } from 'vuex';
    const imagesContext = require.context('../assets', true) 
    export default {    
        components: {
            'app-product-param': ProductParam,
        }, 
        props: ['item'],
        methods: {
            ...mapMutations([
                'addToOrder',
                'delFromOrderByKey',
                'delAllFromOrderByKey',
            ]),
        },
        data() {
            return {
                images: imagesContext, 
            }
        },
    }
</script>

<style scoped>

    td {          
        text-align: left;
        vertical-align: middle;        
        border-bottom: 1px solid #DDD;
        padding-top: 10px;   
        padding-bottom: 10px;
    }

    .order-item {    
        width: 100%;
    }

    .image {        
        width: 100px;
        height: 100px;        
        background-image: require('../assets/noimage.svg');
    }

    .name {        
        margin-top: 5px;
        font-size: 1.0em;      
        margin-bottom: 5px;          
    }

    .description {        
        color: #777;        
    }

    .price {
        font-size: 1.2em;        
        min-width: 70px;
        text-align: right;
        padding-right: 5px;
    }

    .qauntity-cont {                
        min-width: 90px;        
    }
    /* #f0712c; */
    .small-button {            
        border: none;
        border-radius: 10px; 
        background-color: white;
        color: #f0712c;
        padding: 7px 0px 7px 0px;
        width: 20px;       
        font-weight: bold;
        font-size: 1.2em; 
        transition: all 0.1s ease;
    }

    .small-button:hover {                    
        border-radius: 10px;         
        color: black;
    }

    .quantity {     
        border: none;   
        padding: 7px 5px 7px 5px;                        
        font-size: 1.2em;  
    }

</style>




